import React, { useEffect, useState } from "react";
import { Table } from "react-bootstrap";
import 'bootstrap/dist/css/bootstrap.min.css';  // Import Bootstrap CSS
import {
    Container,
    Row,
    Col,
} from "react-bootstrap";
import { useJsApiLoader, GoogleMap, Marker, InfoWindow } from "@react-google-maps/api";
import axios from "axios";
import {forEach} from "react-bootstrap/ElementChildren";

const center = { lat: 7.8731, lng: 80.7718 }; // Default center

export default function MapCustomer() {
    const { isLoaded, loadError } = useJsApiLoader({
        googleMapsApiKey: "AIzaSyDyS9bX9HjvrbKccqFsnuuxn_E5eh3x21I", // Replace with your Google Maps API key
        libraries: ["geometry"],
    });

    const [map, setMap] = useState(/** @type google.maps.Map */ (null));
    const [locations, setLocations] = useState([]); // Array of location objects (latitude, longitude)
    const [selectedLocation, setSelectedLocation] = useState(null);
    const [customer, setCustomer] = useState(null);
    const [customerDetails, setCustomerDetails] = useState([]);
    const [selectedCustomerName, setSelectedCustomerName] = useState(null);


    useEffect(() => {

        axios.get(`https://maxol-sales-rep-track-api-akk9s.ondigitalocean.app/getAllCustomerDetails`, {
            headers:{
                'access-token' : localStorage.getItem("token")
            }

        })
            .then(response => {
                setCustomerDetails(response.data);
            })
            .catch(error => {
                console.error("Error loading locations:", error);
            });

    }, []);

    // Fetch customer details when a marker is clicked
    // Simulate fetching customer details when a marker is clicked
    const tolerance = 0.0001; // Adjust the tolerance as needed

    const fetchCustomerDetails = (location) => {
        console.log("Clicked Location:", location);

        const matchingCustomer = customerDetails.find((customer) => {
            const latMatch = Math.abs(customer.lat - location.lat) < tolerance;
            const lngMatch = Math.abs(customer.lng - location.lng) < tolerance;

            console.log("Customer:", customer);
            console.log("Lat Match:", latMatch);
            console.log("Lng Match:", lngMatch);

            return latMatch && lngMatch;
        });

        if (matchingCustomer) {
            setCustomer(matchingCustomer);
        } else {
            console.log("Customer not found for location:", location);
        }
    };



    if (!isLoaded || loadError) {
        return <p>Loading... Please wait.</p>;
    }

    return (
        <Container className="alog">
            <Row>
                <Col sm={4}>
                    <div className="p-4 h-100 bg-white shadow">
                        {/* Details Side */}
                        <h2>DETAILS</h2>
                        {customer ? (
                            <Table striped bordered hover responsive>
                                <tbody>
                                <tr>
                                    <th>Name:</th>
                                    <td>{customer.name}</td>
                                </tr>
                                <tr>
                                    <th>Contact:</th>
                                    <td>{customer.mobileNo}</td>
                                </tr>
                                <tr>
                                    <th>Address:</th>
                                    <td>{customer.address}</td>
                                </tr>
                                {/* Add more customer details here */}
                                </tbody>
                            </Table>
                        ) : (
                            <p>Select a marker to view customer details</p>
                        )}
                    </div>
                </Col>
                <Col sm={8}>
                    <div className="position-relative" style={{ height: "100vh" }}>
                        {/* Google Map Box */}
                        <GoogleMap
                            center={center}
                            zoom={12}
                            mapContainerStyle={{ width: "100%", height: "100%" }}
                            options={{
                                zoomControl: true,
                                streetViewControl: true,
                                mapTypeControl: true,
                                fullscreenControl: true,
                            }}
                            onLoad={(map) => setMap(map)}
                        >
                            {customerDetails.map((location, index) => {
                                const label = customerDetails[index].name;
                                return (
                                    <Marker
                                        key={index}
                                        position={location}
                                        icon={{
                                            url: `data:image/svg+xml;charset=UTF-8,` +
                                                `<svg width='120' height='40' xmlns='http://www.w3.org/2000/svg'>` +
                                                `<rect width='120' height='40' rx='10' ry='10' fill='white' stroke='orange' stroke-width='2'/>` +
                                                `<text x='50%' y='50%' alignment-baseline='middle' text-anchor='middle' font-size='12' fill='black' >${label}</text>` +
                                                `</svg>`,
                                            scaledSize: new window.google.maps.Size(120, 40),
                                        }}
                                        onClick={() => {
                                            setSelectedLocation(location);
                                            fetchCustomerDetails(location);
                                            // Update selected customer name
                                        }}
                                    />
                                );
                            })}
                            {selectedLocation && (
                                <InfoWindow
                                    position={selectedLocation}
                                    onCloseClick={() => {
                                        setSelectedLocation(null);
                                        setCustomer(null);
                                            // Close InfoWindow and reset customer details
                                    }}
                                >
                                    <div>
                                        <div style={{ backgroundColor: 'white', padding: '5px', borderRadius: '5px' }}>
                                            <p>{customer ? customer.name : null}</p>
                                        </div>
                                    </div>
                                </InfoWindow>
                            )}
                        </GoogleMap>
                    </div>
                </Col>
            </Row>
        </Container>
    );
}
